<template>
  <div id="home-component" class="container-fluid" style="background: #e3e5eb;">
    <!-- alumno -->
    <div class="row" style="background: #e3e5eb;">
      <div class="col-lg-12 pt-0">
        <div class="card w-100">
          <div class="container mt-3">
            <h5>Nuevo Alumno</h5>
            <hr />
            <v-form v-on:submit.prevent="saveAlumno" ref="form" v-model="validForm" enctype="multipart/form-data">
              <div class="row">
                <div class="col-lg-12">
                  <div class="overline">DATOS DEL ALUMNO</div>
                </div>
                <div class="col-lg-6 col-md-12 col-sm-12">
                  <v-text-field
                    outlined
                    @keyup="consultarDNI('hijo')"
                    v-model="user.dni"
                    id="dni-cdt"
                    label="DNI *"
                  ></v-text-field>
                  <v-text-field outlined v-model="user.nombres" label="Nombre *"></v-text-field>
                  <v-text-field
                    outlined
                    v-model="user.apellido_paterno"
                    label="Apellido Paterno *"
                  ></v-text-field>
                  <v-text-field
                    outlined
                    v-model="user.apellido_materno"
                    label="Apellido Materno *"
                  ></v-text-field>
                  <v-menu
                    ref="menu"
                    v-model="menu"
                    :close-on-content-click="false"
                    transition="scale-transition *"
                    offset-y
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on }">
                      <v-text-field
                        outlined
                        v-model="user.fecha_nacimiento"
                        label="Fecha de Nacimiento *"
                        readonly
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      ref="picker"
                      v-model="user.fecha_nacimiento"
                      :max="new Date().toISOString().substr(0, 10)"
                      min="1950-01-01"
                      @change="saveDate"
                    ></v-date-picker>
                  </v-menu>
                  <v-text-field outlined v-model="user.direccion" label="Domicilio *"></v-text-field>
                  <v-text-field outlined v-model="user.telefono" label="Telefono Casa *"></v-text-field>
                </div>
                <div class="col-lg-6 col-md-12 col-sm-12">
                  <v-combobox
                    outlined
                    v-model="user.nivel"
                    @change="updateListGrados"
                    :items="basecolegio.niveles"
                    label="Nivel *"
                  ></v-combobox>

                  <v-combobox outlined v-model="user.grado" :items="grados" label="Grado *"></v-combobox>
                  <!-- <input type="file" name="image"> -->

                  <v-file-input
                    v-model="user.imagen"
                    :rules="rules"
                    accept="image/png, image/jpeg, image/bmp, image/jpg"
                    placeholder="Click aqui para elegir una imagen.."
                    prepend-icon
                    prepend-inner-icon="mdi-camera"
                    label="Imagen"
                    id="photoInput"
                    outlined
                    :loading="loadingImage"
                    @change="updatePreview()"
                  ></v-file-input>
                  <div
                    class="card w-100 mx-auto d-flex flex-column flex-nowrap align-items-center justify-content-center"
                    style="border:none;"
                  >
                    <img
                      @click="openFI"
                      id="img-preview"
                      src="../../assets/img/no-image.png"
                      alt="img-preview"
                      class="img-fluid"
                      style="max-height: 340px; max-width: 340px; cursor: pointer; border-radius: 50%;"
                    />
                  </div>
                </div>
              </div>
              <!-- parent -->
              <div class="row">
                <div class="col-lg-12">
                  <div class="overline">DATOS DEL APODERADO</div>
                </div>
                <div class="col-lg-6 col-md-12 col-sm-12">
                  <v-text-field
                    @keyup="consultarDNI('padre')"
                    v-model="user.pariente.dni"
                    outlined
                    label="DNI"
                    required
                  ></v-text-field>
                  <v-text-field v-model="user.pariente.nombres" outlined label="Nombres" required></v-text-field>
                  <v-text-field
                    v-model="user.pariente.apellido_paterno"
                    outlined
                    label="Apellido Paterno"
                    required
                  ></v-text-field>
                  <v-text-field
                    v-model="user.pariente.apellido_materno"
                    outlined
                    label="Apellido Materno"
                    required
                  ></v-text-field>
                </div>
                <div class="col-lg-6 col-md-12 col-sm-12">
                  <v-text-field
                    v-model="user.pariente.direccion"
                    outlined
                    label="Domicilio"
                    required
                  ></v-text-field>
                  <v-text-field v-model="user.pariente.correo" :rules="emailRules" outlined label="E-mail" required></v-text-field>
                  <v-text-field v-model="user.pariente.telefono" outlined label="Telefono" required></v-text-field>
                  <v-text-field v-model="user.pariente.celular" outlined label="Celular" required></v-text-field>
                </div>
              </div>
              <div class="row">
                <div class="col-lg-12">
                  <v-btn @click="saveAlumno" color="primary"><i class="ml-auto material-icons mr-2">save</i> Guardar</v-btn>
                </div>
              </div>
            </v-form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import defaultImg from '../../assets/img/no-image.png';
import axios from "axios";
import { mapState } from "vuex";
import Swal from 'sweetalert2'

export default {
  data: () => ({
    validForm: true,
    tokenReniec:
      "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJlbWFpbCI6Imx1aXNtZWxnYXJlam9hbGFyY29uQGdtYWlsLmNvbSJ9.sPuL_WIleq7us1RzH1feIjWG_7NC20Jj_9LBgvpQivU",
    user: {
      nombres: "",
      apellido_paterno: "",
      apellido_materno: "",
      dni: "",
      direccion: "",
      fecha_nacimiento: null,
      edad: "",
      telefono: "",
      nivel: "",
      grado: "",
      imagen: null,
      password: "",
      rol_usuario: "alumno",
      pariente: {
        dni: "",
        nombres: "",
        apellido_paterno: "",
        apellido_materno: "",
        direccion: "",
        correo: "",
        telefono: "",
        celular: ""
      }
    },
    default_user: {
      nombres: "",
      apellido_paterno: "",
      apellido_materno: "",
      dni: "",
      direccion: "",
      fecha_nacimiento: null,
      edad: "",
      telefono: "",
      nivel: "",
      grado: "",
      imagen: null,
      password: "",
      rol_usuario: "alumno",
      pariente: {
        dni: "",
        nombres: "",
        apellido_paterno: "",
        apellido_materno: "",
        direccion: "",
        correo: "",
        telefono: "",
        celular: ""
      }
    },
    grados: "",
    menu: false,
    loadingImage: false,
    rules: [
      value =>
        !value ||
        value.size < 2000000 ||
        "Avatar size should be less than 2 MB!"
    ],
  }),
  computed: {
    ...mapState(["basecolegio"])
  },
  watch: {
    menu(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = "YEAR"));
    }
  },
  mounted() {
    this.removeImagen()
    this.$store.dispatch("getBaseColegio");
    this.$store.dispatch('updateGlobalIndex');
  },
  methods: {
    saveDate(date) {
      this.$refs.menu.save(date);
    },
    async consultarDNI(value) {
      try {
        if (value === "hijo") {
          if (this.user.dni.length >= 8) {
            const url = "http://dniruc.apisperu.com/api/v1/dni/";
            const res = await axios.get(
              url + this.user.dni + "?token=" + this.tokenReniec
            );
            if (res.data) {
              this.user.nombres = res.data.nombres;
              this.user.apellido_paterno = res.data.apellidoPaterno;
              this.user.apellido_materno = res.data.apellidoMaterno;
            }
          }
        } else {
          if (this.user.pariente.dni.length >= 8) {
            const url = "http://dniruc.apisperu.com/api/v1/dni/";
            const res = await axios.get(
              url + this.user.pariente.dni + "?token=" + this.tokenReniec
            );
            if (res.data) {
              this.user.pariente.nombres = res.data.nombres;
              this.user.pariente.apellido_paterno = res.data.apellidoPaterno;
              this.user.pariente.apellido_materno = res.data.apellidoMaterno;
            }
          }
        }
      } catch (error) {}
    },
    openFI() {
      const fileInput = document.getElementById("photoInput");
      photoInput.click();
    },
    updatePreview() {
      try {
        if (this.user.imagen !== undefined) {
          this.loadingImage = true;
          const reader = new FileReader();
          reader.readAsDataURL(this.user.imagen);
          reader.onload = () => {
            const imgPrev = document.getElementById("img-preview");
            imgPrev.src = reader.result;
            this.loadingImage = false;
          };
        } else {
          this.removeImagen();
        }
      } catch (error) {
        throw error;
      }
    },
    removeImagen() {
      document.getElementById("img-preview").src = defaultImg;
      this.user.image = undefined;
    },
    updateListGrados() {
      try {
        const arrGrados = [];
        const grados = this.basecolegio.grados;
        grados.forEach(grado => {
          if (grado.nivel === this.user.nivel) {
            arrGrados.push(grado.nombre);
          }
        });
        this.grados = arrGrados;
      } catch (error) {
        return;
      }
    },
    async saveAlumno() {
      if(this.verificaCampos()){
        this.user.password = this.user.dni;
        const formUser = new FormData();
        const userMinified = JSON.stringify(this.user);
        formUser.append('image', this.user.imagen);
        formUser.append('user', userMinified);
        const result = await this.$store.dispatch("addAlumno", formUser);
        if (result){
          this.removeImagen();
          this.user = {};
          this.user = this.default_user;
          this.user.imagen = null;
          this.$router.push('/app1/aintranet/alumnos');
        }
      }else{
        Swal.fire(
          'Datos insuficientes',
          'Debes rellenar como mínimo todos los campos obligatorios que contienen (*)',
          'error'
        )
      }
      
    },
    verificaCampos() {
      if((this.user.nombres === "") || (this.user.apellido_paterno === "") || (this.user.apellido_materno === "") 
      || (this.user.dni === "") || (this.user.direccion === "") || (this.user.fecha_nacimiento === "") || (this.user.dni === "")) {
        return false;
      }
      return true;
    },
  }
};
</script>

<style>
</style>